<template>
  <div id="app">
    <div class="container-fluid" style="padding: unset">
      <Header school='东北大学'></Header>
      <ProjectTitle v-if="projectInfo" :data="projectInfo"></ProjectTitle>
      <TeamTab :data="tabData" :active-index="1" :type="1" v-on:save-team="saveTeam" style="display: none"></TeamTab>
      <div class="container team-container">
        <div class="bar team-title">
          <div class="logo"></div>
          <div class="name"><span v-if="teamInfo.teamName">{{teamInfo.teamName}}</span></div>
          <el-button @click="saveTeam" type="primary" class="pull-right mt-4">完成组队</el-button>
          <el-button class="pull-right mt-4" @click="deleteTeam">解散团队</el-button>
<!--          <div class="set" @click="centerDialogVisible = true"><i class="iconfont icongerenshouye-shezhi1"></i></div>-->
        </div>
        <div class="main">
          <div class="apply-title">
            <div class="vertical"></div>
            <div class="title">成员列表</div>
            <div class="line"></div>
          </div>
          <div class="member-list">
            <div class="box member-box" v-for="obj in teamInfo.memberList" v-bind:key="obj.userCode">
              <div class="model"></div>
              <div class="action">
                <i class="iconfont iconjujue" @click="deleteMember(obj.userCode, obj.teamRole)"></i>
                <span>移出团队</span>
              </div>
              <div class="name">{{obj.userName}}</div>
              <div class="position">{{obj.teamRole === 'leader' ? '队长' : '队员'}}</div>
              <div class="head-portrait"></div>
            </div>
            <div class="box add-box" @click="inviteOthers">
              <i class="iconfont iconyaoqinghaoyou"></i>
              <span>邀请加入</span>
            </div>
          </div>
        </div>
        <div class="apply">
          <div class="apply-title">
            <div class="vertical"></div>
            <div class="title">申请列表</div>
            <div class="line"></div>
          </div>
          <div class="member-list apply-list">
            <div class="box member-box apply-box" v-for="obj in teamInfo.applyList" v-bind:key="obj.userCode">
              <div class="action">
                <i class="iconfont iconduihao" @click="agreeMember(obj.userCode)"></i>
                <i class="iconfont iconjujue" @click="rejectMember(obj.userCode)"></i>
              </div>
              <div class="name">{{obj.userName}}</div>
<!--              <div class="time">申请时间：2019.07.08</div>-->
              <div class="head-portrait"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      :visible.sync="deleteDialogVisible"
      width="468px"
      center>
      <div class="center-block tip-content">
        是否确定解散队伍
      </div>
      <span slot="footer" class="dialog-footer">
         <el-button style="width:140px;" type="primary" @click="centerDialogVisible = false">确定</el-button>
        <el-button style="width:140px;" type="primary" @click="centerDialogVisible = false">取消</el-button>
      </span>
    </el-dialog>
    <el-dialog
      :visible.sync="confirmDialogVisible"
      title="是否确定完成组队并开始项目？"
      width="468px"
      center>
      <div class="confirm-content">
        <div class="box member-box" v-for="obj in teamInfo.memberList" v-bind:key="obj.userCode">
          <div class="name">{{obj.userName}}</div>
          <div class="position">{{obj.teamRole === 'leader' ? '队长' : '队员'}}</div>
          <div class="head-portrait"></div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
         <el-button style="width:140px;" type="primary" @click="completeTeam">确定</el-button>
        <el-button style="width:140px;" type="primary" @click="confirmDialogVisible = false">取消</el-button>
      </span>
    </el-dialog>
    <el-dialog
      :visible.sync="deleteDialogVisible"
      width="468px"
      center>
      <div class="center-block tip-content">
        是否确定解散队伍
      </div>
      <span slot="footer" class="dialog-footer">
         <el-button style="width:140px;" type="primary" @click="centerDialogVisible = false">确定</el-button>
        <el-button style="width:140px;" type="primary" @click="centerDialogVisible = false">取消</el-button>
      </span>
    </el-dialog>
    <el-dialog
      :visible.sync="inviteDialogVisible"
      title="请选择"
      width="468px"
      center>
      <div>
        <el-select v-model="classKey" clearable placeholder="请选择班级" style="width: 40%">
          <el-option
            v-for="item in classData"
            :key="item.classId"
            :label="item.className"
            :value="item.classId">
          </el-option>
        </el-select>
        <el-input placeholder="姓名" v-model="nameKey" style="width: 35%;margin-left: 5%"></el-input>
        <el-button style="width:70px" @click="inviteOthers" type="primary">搜索</el-button>
      </div>
      <div class="confirm-content">
        <div class="box member-box" v-for="obj in invitableMembersInfo" v-bind:key="obj.userCode">
          <div class="name">{{obj.userRealName}}</div>
          <div class="position" @click="inviteMember(obj.userCode)">邀请加入</div>
          <div class="head-portrait"></div>
        </div>
      </div>
      <div v-if="invitableMembersInfo">
        <el-pagination
          background
          layout="prev, pager, next"
          :hide-on-single-page="true"
          :total="totalMembers"
          :page-size="pageSize"
          :current-page="currPage"
          :page-count="pageCount"
          @current-change="paginationChange"
        >
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Header from '../../../../components/school-training/Header.vue'
import ProjectTitle from '../../../../components/school-training/ProjectTitle.vue'
import TeamTab from '../../../../components/school-training/TeamTab.vue'
export default {
  components: {
    Header,
    ProjectTitle,
    TeamTab
  },
  name: 'app',
  mounted () {
    this.loadData()
  },
  data () {
    return {
      form: {
        name: '某某某的团队'
      },
      centerDialogVisible: false,
      deleteDialogVisible: false,
      confirmDialogVisible: false,
      inviteDialogVisible: false,
      searchKey: '',
      tabData: [{
        name: '选择项目',
        url: 'course-detail.html'
      }, {
        name: '组队',
        url: 'team-search.html'
      }, {
        name: '任务裁剪',
        url: ''
      }, {
        name: '人员管理',
        url: ''
      }],
      teamData: [],
      teamInfo: null,
      projectInfo: null,
      invitableMembersInfo: null,
      classData: [],
      classKey: null,
      nameKey: null,
      currPage: 1,
      pageSize: 10,
      pageCount: 0,
      totalMembers: 0
    }
  },
  methods: {
    loadData () {
      this.loadProjectInfo()
      this.loadTeamList()
      this.loadInvitableMembers()
      this.loadClassData()
    },
    loadTeamList () {
      let me = this
      this.$http.post('/teambuild/queryteaminfoforleader', {
      }, res => {
        if (res.success) {
          me.teamInfo = res.data
        }
      })
    },
    loadProjectInfo () {
      let me = this
      let projectId = localStorage.getItem('projectId')
      let courseId = localStorage.getItem('courseId')
      this.$http.post('/teambuild/queryprojectinfo', {
        projectId: projectId,
        courseId: courseId
      }, res => {
        if (res.success) {
          me.projectInfo = res.data
          me.projectInfo.courseName = localStorage.getItem('courseName')
        }
      })
    },
    loadInvitableMembers () {
      let me = this
      me.$http.post('/teambuild/queryinvitablemember', {
        grade: localStorage.getItem('userEnterYear'),
        classId: me.classKey,
        userName: me.nameKey,
        currPage: me.currPage,
        pageSize: me.pageSize
      }, res => {
        if (res.success) {
          me.pageCount = res.data.totalPageNum
          me.totalMembers = res.data.totalCount
          me.invitableMembersInfo = res.data.data
          console.log(me.invitableMembersInfo)
        }
      })
    },
    loadClassData () {
      let me = this
      me.$http.post('/user/queryclass', { 'userEnterYear': localStorage.getItem('userEnterYear') }, res => {
        // console.log(res.data)
        me.classData = res.data
      })
    },
    deleteTeam () {
      let me = this
      // this.deleteDialogVisible = true
      this.$confirm('是否确定解散队伍?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.post('/teambuild/dismissteam', {
          teamId: me.teamInfo.teamId
        }, res => {
          if (res.success) {
            this.$message('已删除')
            window.location = 'team-search.html'
          }
        })
      })
    },
    saveTeam () {
      this.confirmDialogVisible = true
    },
    agreeMember (userCode) {
      let me = this
      this.$http.post('/teambuild/agreerefuseinvitecaptain', {
        teamId: me.teamInfo.teamId,
        joinStatus: 1,
        userCode: userCode
      }, res => {
        if (res.success) {
          me.$message('已同意成员申请')
          me.loadTeamList()
        }
      })
    },
    rejectMember (userCode) {
      let me = this
      this.$http.post('/teambuild/agreerefuseinvitecaptain', {
        teamId: me.teamInfo.teamId,
        joinStatus: -1,
        userCode: userCode
      }, res => {
        if (res.success) {
          me.$message('已拒绝成员申请')
          me.loadTeamList()
        }
      })
    },
    // 完成组队开始项目
    completeTeam () {
      // window.location = 'project-task-review.html'
      let me = this
      if (me.teamInfo.memberList.length < me.projectInfo.minCount || me.teamInfo.memberList.length > me.projectInfo.maxCount) {
        me.$alert('团队人数不符合项目要求！', '提示', {
          confirmButtonText: '确定'
        })
        return false
      }
      this.$http.post('/teambuild/startproject', {
        'teamId': me.teamInfo.teamId
      }, res => {
        if (res.success) {
          window.location = 'task-list.html'
        } else {
          // 返回错误的处理
        }
      })
    },
    inviteOthers () {
      let me = this
      if (me.currPage !== 1) {
        me.currPage = 1
        me.loadInvitableMembers()
        me.inviteDialogVisible = true
      } else {
        me.loadInvitableMembers()
        me.inviteDialogVisible = true
      }
    },
    inviteMember (userCode) {
      let me = this
      me.$http.post('/teambuild/sendinvitation', {
        teamId: me.teamInfo.teamId,
        userCode: userCode
      }, res => {
        if (res.success) {
          me.$message('已成功邀请')
        }
      })
    },
    paginationChange (currentPage) {
      this.currPage = currentPage
      this.loadInvitableMembers()
    },
    deleteMember (userCode, userRole) {
      let me = this
      if (userRole === 'leader') {
        this.$alert('不能移除队长！')
        return false
      }
      this.$confirm('是否删除队员?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        me.$http.post('/teambuild/deleteteambuildmember', {
          teamId: me.teamInfo.teamId,
          userCode: userCode
        }, res => {
          if (res.success) {
            me.$message('删除成功！')
            me.loadData()
          }
        })
      })
    }
  }
}
</script>

